	module.exports = {"data":{"markdownRemark":{"html":"<h2>Variadic Functions</h2>\n<p>Variadic functions are functions that can be called with any number of trailing arguments.</p>\n<p>This is a useful feature when we are working on web-apps. </p>\n<p>Sometimes we are not how many elements we will need to pass to the HTML templating engine.</p>\n<p>Here are the basics on how varidic functions work:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-go\"><code><span class=\"token keyword\">package</span> main\n<span class=\"token keyword\">import</span> <span class=\"token string\">\"fmt\"</span>\n\n<span class=\"token keyword\">func</span> <span class=\"token function\">printFruits</span><span class=\"token punctuation\">(</span>fruits <span class=\"token operator\">...</span><span class=\"token builtin\">string</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">for</span> <span class=\"token boolean\">_</span><span class=\"token punctuation\">,</span> fruit <span class=\"token operator\">:=</span> <span class=\"token keyword\">range</span> fruits<span class=\"token punctuation\">{</span>\n        fmt<span class=\"token punctuation\">.</span><span class=\"token function\">Println</span><span class=\"token punctuation\">(</span>fruit<span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">func</span> <span class=\"token function\">main</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n   <span class=\"token function\">printFruits</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"apple\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"bannana\"</span><span class=\"token punctuation\">)</span>\n   <span class=\"token function\">printFruits</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"papaya\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"coconut\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"pear\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"pineapple\"</span><span class=\"token punctuation\">)</span>\n   berries <span class=\"token operator\">:=</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token builtin\">string</span><span class=\"token punctuation\">{</span><span class=\"token string\">\"blueberry\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"strawberry\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"raspberry\"</span><span class=\"token punctuation\">}</span>\n   <span class=\"token function\">printFruits</span><span class=\"token punctuation\">(</span>berries<span class=\"token operator\">...</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>First, in printFruits we defined the number of arguments with […string].</p>\n<p>This tells Go that this function accepts any number of string arguments.</p>\n<p>The first two calls to printFruits shows that the function will print each string, even if we are passing a different number of arguments.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code>apple\nbannana\n...\npapaya\ncoconut\npear\n...</code></pre>\n      </div>\n<p>This will also work for slices.</p>\n<p>To use a vardiac function with a slice, we add the training dots to the call.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-go\"><code><span class=\"token function\">printFruits</span><span class=\"token punctuation\">(</span>berries<span class=\"token operator\">...</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code>blueberry\nstrawberry\nraspberry</code></pre>\n      </div>","fields":{"slug":"/go/variadic-functions/"},"frontmatter":{"title":"Variadic Functions"}}},"pathContext":{"slug":"/go/variadic-functions/"}}

